#name{
    font-family: 'PT Sans Caption', sans-serif;
    margin: auto;
    margin-top: 100px;
    transition: width 2s;
}

a {
margin: 0px;
text-decoration: none;
}

i{
    font-size: 36px;
    margin: 10px 50px;
    text-decoration: none;
}

i:hover {
    color: rgb(216, 215, 215);
    cursor: pointer;
    transform: scale(1.2);
} 
#linkedin:hover {
    color:#0e76a8
} 
#fb:hover {
    color: #3b5998;
}
#phone:hover{
    animation: shake 0.4s;
}
#email:hover {
     transition: width 2s, height 2s, transform 2s 
}